import React from 'react';
import Footer from './components/Footer';
import Header from './components/Header';
import './Sobre.css';
import SliderApps from './components/sliders/SliderApps';

const Onboarding = () =>
  <div className="About">
    <Header title='Apps Elithium'></Header>

    <section className="p-sections">
      <div className="container">
        <div className="row">
          <div className="col-md-5">
            <img src="/app-tracking.png" alt="app-tracking" className="img-responsive" />
          </div>
          <div className="col-md-7">
            <h2 className="title-sections">Monitoramento e Gestão</h2>
            <p>O SSX Onboard e SXX Tracking são aplicativos desenvolvidos para funcionar em conjunto com o sistema SSX de
              rastreamento e gestão.
              </p>
            <p>São Apps Ideais  para quem precisa gerenciar sua frota on the go..
              </p>
          </div>
        </div>
      </div>
    </section>

    <section className="bg-sections-gradient">
      <div className="container">
        <div className="row">
          <div className="col-md-offset-1 col-md-4">
            <h2 className="title-sections c-white">Funcionalidades</h2>
            <p className="c-white">• Ver todos os veículos de sua frota filtrados por atualizados e desatualizados,</p>
            <p className="c-white">• Ver todos os veículos filtrados por um determinado alerta, </p>
            <p className="c-white">• Visualizar seus veículos no mapa,</p>
          </div>
          <div className="col-md-offset-2 col-md-4">
            <h2 className="title-sections c-white">Diferenciais</h2>
            <p className="c-white">• Visualizar histórico do veículo no mapa,</p>
            <p className="c-white">• Visualizar detalhes da posição de seus veículos,</p>
            <p className="c-white">• Enviar comandos para seus veículos.</p>
          </div>
        </div>
      </div>
    </section>

    <section>
      <div className="container">
        <div className="row">
          <div className="col-md-offset-2 col-md-8">
            <SliderApps></SliderApps>
          </div>
        </div>
      </div>
    </section>

    <section className="p-sections">
      <div className="bg-image-full bg-solution-full">
        <div className="container">
          <div className="row">
            <div className="col-md-offset-7 col-md-4">
              <h2 className="title-sections">Baixe nosso guia completo de soluções</h2>
              <a href="/docs/Apresentacao-SSX-Elithium.pdf" target="_blank" className="rounded-button">Baixar Agora</a>
            </div>
          </div>
        </div>
      </div>
    </section>

    <Footer></Footer>

  </div>;


export default Onboarding;
