import React from 'react';
import './Blog.css';
import * as axios from 'axios';
import { snakeCase, replace } from 'lodash'
import { Link } from 'react-router-dom'
// import { url } from 'inspector';

class BlogHome extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            posts: []
        }
    }

    componentDidMount() {
        axios.get(`https://api.elithium.tk/api/blog`)
            .then(res => {
                const posts = res.data;
                this.setState({ posts });
                console.log(posts);
            })
    }

    render() {

        return (
            <div className="row">
                {this.state.posts.map(post =>
                    <div className="col-md-4" key={post._id}>
                        <Link to={{
                            pathname: `/blog/${replace(snakeCase(post.title), '_', '-')}`,
                            state: { blogId: post._id }
                        }} >
                            <div className="blogBoxHome"
                                style={{ backgroundImage: `url(https://api.elithium.tk/${post.coverImage})` }}>
                                <div className="title-post-home">
                                    <p>{post.title}</p>
                                </div>
                            </div>
                        </Link>
                    </div>)}
            </div>
        );
    }
}

export default BlogHome;



