import React from 'react';
import ReactSwipe from 'react-swipe';
import './Sliders.css';

class SliderApps extends React.Component {

  render() {
    let reactSwipeEl;
    return (

      <div className="sliders text-center">
        <ReactSwipe
          className="carousel"
          swipeOptions={{ continuous: true, auto: 5000, }}
          ref={el => (reactSwipeEl = el)}
        >
          <div>
            <h2>App Onboard</h2>
            <img src="/apps/apps1.png" alt="app1" className="img-responsive" />
            <p><strong>Jornada de Trabalho</strong></p>
            <p>O Controle de Jornada de Trabalho Onboard permite que o usuário acompanhe
                os dados de cada motorista diretamente do aplicativo.</p>
          </div>
          <div>
            <h2>App Onboard</h2>
            <img src="/apps/apps2.png" alt="app2" className="img-responsive" />
            <p><strong>Troca de Mensagens</strong></p>
            <p>Na área de Troca de Mensagens a Central pode enviar mensagens de áudio para o motorista.
            A mensagem pode ser executada sem a necessidade do desbloqueio do smartphone, assim a mensagem
              chega mesmo com a tela bloqueada sem tirar a atenção do motorista.</p>
          </div>
          <div>
            <h2>App Onboard</h2>
            <img src="/apps/apps3.png" alt="app3" className="img-responsive" />
            <p><strong>Navegação</strong></p>
            <p> Na área de Navegação o usuário pode acompanhar a rota do veículo pelo aplicativo, definida na
              etapa de planejamento de viagem. No caso da necessidade do cálculo de uma nova rota devido a
              desvios, o usuário poderá optar por exportar o destino para outro aplicativo de navegação como
              Google Maps ou Waze.
              </p>
          </div>
          <div>
            <h2>App Tracking</h2>
            <img src="/apps/apps4.png" alt="app3" className="img-responsive" />
            <p> O SSX Tracking foi desenvolvido pra quem precisa gerenciar sua frota on the go.
              </p>
          </div>
          <div>
            <h2>App Tracking</h2>
            <img src="/apps/apps5.png" alt="app3" className="img-responsive" />
            <p><strong>Funcionalidades:</strong></p>
            <p>•	 Ver todos os veículos de sua frota
            filtrados por atualizados e desatualizados, </p>
            <p>•	 Ver todos os veículos filtrados por um
            determinado alerta, </p>
            <p>•	 Visualizar seus veículos no mapa, </p>
            <p>•	 Visualizar histórico do veículo no mapa, </p>
            <p>•	 Visualizar detalhes da posição
            de seus veículos, </p>
            <p>•	 Enviar comandos para seus veículos.</p>
          </div>

        </ReactSwipe>
        <button onClick={() => reactSwipeEl.next()} className="button-slider">
            <i className="fa fa-chevron-left"></i>
          </button>
          <button onClick={() => reactSwipeEl.prev()} className="button-slider">
            <i className="fa fa-chevron-right"></i>
          </button> 
      </div>
    );
  }
}

export default SliderApps;