import React from 'react';
import Footer from './components/Footer';
import Header from './components/Header';
import './Sobre.css';

const Sobre = () =>
  <div className="About">
    <Header title='A Empresa'></Header>

    <section className="p-sections">
      <div className="container">
        <div className="row">
          <div className="col-md-5">
            <img src="/about.png" alt="sobre" className="img-responsive" />
          </div>
          <div className="col-md-7">
            <h2 className="title-sections">Soluções Inteligentes</h2>
            <p>A Elithium é uma empresa de Soluções para Gestão de Frota, nosso foco é atender necessidades que
              possam reduzir consideravelmente o custo operacional da frota desenvolvendo soluções de telemetria,
              logística e jornada de trabalho, não esquecendo da questão da segurança contra roubo e
              furto do veículo ou da carga.
              </p>
            <p>Também atendemos necessidades na área de segurança do trabalho, oferendo soluções para a
              prevenção da fadiga ou distração do condutor, diminuindo consideravelmente o risco de
              acidentes de trabalho.
              </p>
          </div>
        </div>
      </div>
    </section>

    <section className="bg-sections-gradient">
      <div className="container">
        <div className="row">
          <div className="col-md-offset-1 col-md-4">
            <h2 className="title-sections c-white">Objetivo</h2>
            <p className="c-white">O objetivo de toda empresa é gerar lucro, conosco não é diferente,
              dessa forma a empresa pode reinvestir o lucro em melhorias, novas soluções,
              pagando bons salários e contratando novos colaboradores capacitados.
            </p>
          </div>
          <div className="col-md-offset-2 col-md-4">
            <h2 className="title-sections c-white">Visão</h2>
            <p className="c-white">“Pensar grande e pensar pequeno, dá o mesmo trabalho”. Essa é uma frase que temos
              em mente e tentamos colocar em prática. Pensamos alto, em como a empresa pode
              concorrer de forma saudável com grandes players do mercado de rastreamento e
              gestão de frota e acreditamos que em 2022 podemos ser um dos destaques desse
              segmento que tem crescido tanto no Brasil.
            </p>
          </div>
        </div>
      </div>
    </section>

    <section className="p-sections">
      <div className="bg-image-full bg-call-to-action-section">
        <div className="container">
          <div className="row">
            <div className="col-md-offset-7 col-md-4">
              <h2 className="title-sections">Conheça Nossas Soluções</h2>
              <a href="#" className="rounded-button">Soluções</a>
            </div>
          </div>
        </div>
      </div>
    </section>

    <Footer></Footer>

  </div>;


export default Sobre;
