import React from 'react';
import './Footer.css';
import axios from 'axios';

export default class Footer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            email: '',
            name: '',
        }
    }

    render() {
        const { email, name } = this.state;

        return <div id="contact">
            <section className="footer">
                <div className="container">
                    <div className="row">
                        <div className="col-md-4">
                            <h3>Entre em Contato</h3>
                            <p>Praça Berlim Nº 77 - Jardim Europa</p>
                            <p>Sorocaba/SP </p>
                            <p>contato@elithium.com.br  </p>
                            <p>(15) 3359-8302 | (15) 3359-8302</p>
                        </div>
                        <div className="col-md-4">
                            <h3>Redes Sociais</h3>
                            <aside className="social-icons-footer">
                                <ul>
                                    <li>
                                        <a href="https://www.youtube.com/channel/UCAVM2S0v3OFSXCWy0P8itPw" target="_blank">
                                            <i className="fa fa-youtube-play" aria-hidden="true"></i>
                                        </a>
                                    </li>
                                    <li>
                                        <a href="https://bit.ly/2VxOxKd" target="_blank">
                                            <i className="fa fa-linkedin" aria-hidden="true"></i>
                                        </a>
                                    </li>
                                    <li><a href=""><i className="fa fa-google" aria-hidden="true"></i></a></li>
                                </ul>
                            </aside>
                            <a target="_blank" href="https://api.whatsapp.com/send?phone=5511960774040" className="call-to-whatsapp">
                                <i className="fa fa-whatsapp" aria-hidden="true"></i>
                                Chamar no WhatsApp</a>
                        </div>
                        <div className="col-md-4">
                            <h3>Newsletter</h3>
                            <form className="newsletter-form" onSubmit={this.onNewsletterSubscribeClick}>
                                <input placeholder="Nome" className="input-newsletter" value={name} onChange={this.onNameChange} />
                                <input placeholder="E-mail" className="input-newsletter" value={email} onChange={this.onEmailChange} />
                                <button >Cadastre-se</button>
                            </form>
                        </div>
                    </div>
                </div>
            </section>
            <section className="footer-copyright">
                <div className="container">
                    <span> Copyright ® Elithium | Todos os Direitos Reservados</span>
                </div>
            </section>
        </div>;
    }

    onNewsletterSubscribeClick = (e) => {
        e.preventDefault()
        const { name, email } = this.state;
        axios.post('https://api.elithium.tk/api/newsletter', {
            name, email
        }).then(() => {
            console.log('Email cadastrado com sucesso')
            this.setState({
                name: '',
                email: ''
            })
        }).catch(e => {
            console.log('Houve um erro ao cadastrar o email', e);
        })
    }

    onNameChange = (e) => {
        this.setState({
            name: e.target.value
        })
    }

    onEmailChange = (e) => {
        this.setState({
            email: e.target.value
        })
    }
}
