import React from 'react';
import Footer from './components/Footer';
import Header from './components/Header';
import YouTube from 'react-youtube';
import './Cases.css';

class Cases extends React.Component {
  render() {
    const opts = {
      height: '315',
      width: '100%',
      playerVars: { // https://developers.google.com/youtube/player_parameters
        autoplay: 0
      }
    };

    return (
      <div className="Cases">
        <Header title='Cases'></Header>

        <section className="p-sections">
          <div className="container">
            <div className="row">
              <div className="col-md-5">
                <img src="/case.png" alt="cases" className="img-responsive" />
              </div>
              <div className="col-md-7">
                <h2 className="title-sections">Soluções Inteligentes</h2>
                <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt
                  ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco
                  laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in
                  voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat
                  non proident, sunt in culpa qui officia deserunt mollit anim id est laborum
            </p>
              </div>
            </div>
          </div>
        </section>

        <section className="p-sections">
          <div className="container">
            <div className="row">
              <div className="col-md-offset-3 col-md-6">
                <h2 className="title-sections text-center">Assista o case completo</h2>
                <YouTube
                  videoId="05b8WW8s8RI"
                  opts={opts}
                  onReady={this._onReady}
                />
                {/* <iframe width="100%" height="315" src="https://www.youtube.com/embed/05b8WW8s8RI" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe> */}
              </div>
            </div>
          </div>
        </section>

        <section className="p-sections">
          <div className="bg-image-full bg-call-to-action-section">
            <div className="container">
              <div className="row">
                <div className="col-md-offset-7 col-md-4">
                  <h2 className="title-sections">Conheça Nossas Soluções</h2>
                  <a href="#" className="rounded-button">Soluções</a>
                </div>
              </div>
            </div>
          </div>
        </section>

        <Footer></Footer>

      </div>
    );
  }

  _onReady(event) {
    // access to player in all event handlers via event.target
    event.target.pauseVideo();
  }
}

export default Cases;