import React from 'react';
import './Header.css';

class Header extends React.Component {

  constructor() {
    super();

    this.state = {
      showMenu: false,
      showMenuMobile: false
    }

    this.showMenu = this.showMenu.bind(this);
    this.showMenuMobile = this.showMenuMobile.bind(this);
  }

  showMenu(event) {
    event.preventDefault();

    this.setState({
      showMenu: !this.state.showMenu,
    });
  }

  showMenuMobile(event) {
    event.preventDefault();

    this.setState({
      showMenuMobile: !this.state.showMenuMobile,
    });
  }

  render() {
    const { title } = this.props;

    return (

      <div className="">
        <header className="header-intern">
          <div className="container">
            <div className="row">
              <div className="col-md-3">
                <a href="/" className="show">
                  <img src="/logo-color.png" alt="logo" className="img-responsive m-0-auto" />
                </a>
              </div>
              <div className="col-md-7 col-md-offset-2 none-mobile">
                <div className="login-container"><a href="#">login</a></div>
                <nav className="menu">
                  <ul>
                    <li><a href="/">Home</a></li>
                    <li><a href="/sobre">A Empresa</a></li>
                    <li className="sub-menu"><a href="/" onClick={this.showMenu}>Soluções</a>
                      {
                        this.state.showMenu
                          ? (
                            <ul>
                              <li> <a href="/solucoes/sistema-ssx">Sistema SSX </a> </li>
                              <li> <a href="/solucoes/apps-onboarding-tracking">Apps Elithium </a> </li>
                              <li> <a href="/solucoes/sensor-fadiga">Sensor de Fadiga </a> </li>
                            </ul>
                          )
                          : (
                            null
                          )
                      }
                    </li>
                    <li><a href="/cases">Cases</a></li>
                    <li><a href="/#contact">Contato</a></li>
                  </ul>
                </nav>
              </div>
              <div className="col-md-12 show-mobile">
            <button className="button-mobile"  onClick={this.showMenuMobile}><i className="fa fa-bars" aria-hidden="true"></i></button> 
            {
              this.state.showMenuMobile ? (
            <ul className="menu-mobile-home">
              <li><a href="/">Home</a></li>
              <li><a href="/sobre">A Empresa</a></li>
              <li> <a href="/solucoes/sistema-ssx">Sistema SSX </a> </li>
              <li> <a href="/solucoes/apps-onboarding-tracking">Apps Elithium </a> </li>
              <li> <a href="/solucoes/sensor-fadiga">Sensor de Fadiga </a> </li>
              <li><a href="/cases">Cases</a></li>
              <li><a href="/#contact">Contato</a></li>
              <a href="#" className="login-home">login</a>
              </ul>
              ) : ( null ) } 
          </div>
            </div>
          </div>
        </header>
        <div className="banner-header">
          <div className="container">
            <h1>{title}</h1>
          </div>
        </div>
      </div>
    );
  }
}

export default Header;



