import React from 'react';
import ReactSwipe from 'react-swipe';
import './Sliders.css';

class SliderSensorFadiga extends React.Component {

  render() {
    let reactSwipeEl;
    return (

      <div className="sliders text-center">
        <ReactSwipe
          className="carousel"
          swipeOptions={{ continuous: true, auto: 5000, }}
          ref={el => (reactSwipeEl = el)}
        >
          <div>
            <img src="/vuematedl550a.png" alt="sensor-de-fadiga" className="img-responsive" />
            <p>Sistema de alerta de sonolência para motorista capaz de identificar distração e fadiga ajudando na prevenção de acidentes.</p>
          </div>
          <div>
            <img src="/appVue.png" alt="app" className="img-responsive" />
            <p>Painel de dashboard e app do motorista: ranking de motorista, consumo do veículo, jornada de trabalho, logística e alertas</p>
          </div>
        </ReactSwipe>
        <button onClick={() => reactSwipeEl.next()} className="button-slider">
            <i className="fa fa-chevron-left"></i>
          </button>
          <button onClick={() => reactSwipeEl.prev()} className="button-slider">
            <i className="fa fa-chevron-right"></i>
          </button> 
      </div>
    );
  }
}

export default SliderSensorFadiga;