import React from 'react';
import Footer from './components/Footer';
import Header from './components/Header';
import './Sobre.css';
import SliderSsx from './components/sliders/SliderSsx';

const Ssx = () =>
  <div className="About">
    <Header title='Sistema SSX'></Header>

    <section className="p-sections">
      <div className="container">
        <div className="row">
          <div className="col-md-5">
            <img src="/ssx.png" alt="ssx" className="img-responsive" />
          </div>
          <div className="col-md-7">
            <h2 className="title-sections">Gestão, Telemetria e Logística</h2>
            <p>O SSX é um sistema desenvolvido para prover rastreamento veicular e monitoramento além de
               soluções de segurança, desempenho e logística.</p>
            <p>A versão completa do sistema oferece um serviço abrangente de rastreamento e monitoramento.
              Incluindo recursos mais sofisticados para análises de performance, logística e segurança.</p>
          </div>
        </div>
      </div>
    </section>

    <section className="bg-sections-gradient">
      <div className="container">
        <div className="row">
          <div className="col-md-offset-1 col-md-4">
            <h2 className="title-sections c-white">Funcionalidades</h2>
            <p className="c-white">• Aumentar a segurança no transporte de cargas, </p>
            <p className="c-white">• Avaliar o desempenho operacional dos motoristas na utilização de veículos
             e máquinas,</p>
            <p className="c-white">• Planejar e controlar, em tempo real, operações de coleta,
             entrega e atendimento a serviços externos,</p>
            <p className="c-white">• Evitar ações trabalhistas e reduzir a quantidade de horas extras
             controlando a jornada de trabalho do motorista.
            </p>
          </div>
          <div className="col-md-offset-2 col-md-4">
            <h2 className="title-sections c-white">Vantagens</h2>
            <p className="c-white">• Modelo de negócio personalizado de acordo com a necessidade de cada cliente,</p>
            <p className="c-white">• Sistema desenvolvido com foco na utilização do usuário: bonito, rápido, intuitivo e fácil de usar,</p>
            <p className="c-white">• Aplicativos embarcados para estender o controle operacional até os veículos,</p>
            <p className="c-white">• Aplicativos específicos para atender as necessidades de empresas e pessoas,</p>
            <p className="c-white">• Redução de custo operacional em até 25%.</p>
          </div>
        </div>
      </div>
    </section>

    <section>
      <div className="container">
        <div className="row">
          <div className="col-md-offset-2 col-md-8">
            <SliderSsx></SliderSsx>
          </div>
        </div>
      </div>
    </section>

    <section className="p-sections">
      <div className="bg-image-full bg-solution-full">
        <div className="container">
          <div className="row">
            <div className="col-md-offset-7 col-md-4">
              <h2 className="title-sections">Baixe nosso guia completo de soluções</h2>
              <a href="/docs/Apresentacao-SSX-Elithium.pdf" target="_blank" className="rounded-button">Baixar Agora</a>
            </div>
          </div>
        </div>
      </div>
    </section>

    <Footer></Footer>

  </div>;


export default Ssx;
