import React from 'react';
import './Home.css';
import Footer from './components/Footer';
import HeaderHome from './components/HeaderHome';
import Testimonials from './components/sliders/Testimonials';
import BlogHome from './components/BlogHome';

class Home extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
          field1: '', 
          field2: '',
          resultado: ''
        };
  
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange(event) {
        this.setState({
            field1: event.target.field1,
            field2: event.target.field2
        });
      }
  
    handleSubmit(event) {
        console.log(this.state.field1, this.state.field2);
        alert('A name was submitted: ' + this.state.field1 + this.state.field2);
        this.setState({
            resultado: this.state.field2
        })
        console.log(this.state.resultado);
        event.preventDefault();
    }

    render() {

        const { resultado } = this.state; 
        return(
        
        <div className="Home">
        <HeaderHome></HeaderHome>

        <section className="about">
            <div className="container">
                <h2>Prazer somos a <img src="/logo-about.png" alt="Logo Elithium" className="" /></h2>
                <div className="row">
                    <div className="col-md-offset-4 col-md-8">
                        <p>A Elithium é uma empresa de Soluções para Gestão de Frota, nosso foco é atender
    necessidades que possam reduzir consideravelmente o custo operacional da frota
    desenvolvendo soluções de telemetria, logística e jornada de trabalho, não esquecendo da
    questão da segurança contra roubo e furto do veículo ou da carga.
                    </p>
                    </div>
                </div>
            </div>
        </section>

        <section className="services-home">
            <div className="container">
                <div className="row p-b-60 p-t-60">
                    <div className="col-md-3">
                        <h2 className="title">Soluções Eficientes e com alto custo benefício</h2>
                    </div>
                    <div className="col-md-3">
                        <a href="/solucoes/sistema-ssx" className="home-solution home-solution-ssx">
                            <h3>Sistema <br /> SSX</h3>
                        </a>
                    </div>
                    <div className="col-md-3">
                        <a href="/solucoes/apps-onboarding-tracking" className="home-solution home-solution-onboarding">
                            <h3>App <br /> SSX Tracking + onboarding</h3>
                        </a>
                    </div>
                    <div className="col-md-3">
                        <a href="/solucoes/sensor-fadiga" className="home-solution home-solution-tracking">
                            <h3>Sensor <br /> de Fadiga</h3>
                        </a>
                    </div>
                </div>
            </div>
        </section>

        <section className="solutions">
            <div className="container">
                <div className="row">
                    <div className="col-md-5">
                        <img src="/desktop.png" alt="Desktop" className="img-responsive" />
                    </div>
                    <div className="col-md-offset-1 col-md-6">
                        <h2>O que nós oferecemos</h2>
                        <h4>Controle de Velocidade</h4>
                        <p>Sistema identifica toda vez que o veiculo ultrapassa a velocidade estipulada, seja em tempo seco ou chuvoso, além do disparo de sirene na cabine do veiculo.</p>
                        <h4>Módulo de Transporte/Roteirizador</h4>
                        <p>O Modulo de Transporte tem como objetivo auxiliar a logística da empresa, criando rotas dinâmicas para reduzir tempo e distancia percorrida. </p>
                        <h4>Módulo de Manutenção Preventiva</h4>
                        <p>Essa ferramenta foi criada para auxiliar o gestor da frota no agendamento e controle das manutenções preventivas podendo enviar os avisos por email para o gestor ou oficina cadastrada. </p>
                    </div>
                </div>
            </div>
        </section>

        <section className="apps">
            <div className="container">
                <div className="row">
                    <div className="col-md-7">
                        <h2>Apps Elithium</h2>
                        <h4>Oferecemos gratuitamente aplicativos a nossos clientes</h4>
                        <div className="divider"></div>
                        <div className="row">
                            <div className="col-md-6">
                                <h4>App Tracking</h4>
                                <p>Para acompanhamento e rastreamento pelo smartphone ou tablet.</p>
                            </div>
                            <div className="col-md-6">
                                <h4>App Onboarding</h4>
                                <p>O motorista pode trocar informações em tempo real com a central, informando se a entrega foi efetuada, abertura ou fechamento de O.S e etc. </p>
                            </div>
                        </div>
                        <a href="/solucoes/apps-onboarding-tracking" className="button">Saiba Mais</a>
                    </div>
                    <div className="col-md-offset-1 col-md-4">
                        <img src="/apps-home.png" alt="Aplicativos" className="img-responsive" />
                    </div>
                </div>
            </div>
        </section>

        <section>
            <div className="container">
                <div className="row">
                    <div className="col-md-offset-3 col-md-6">
                        <Testimonials></Testimonials>
                    </div>
                </div>
            </div>
        </section>

        <section className="p-sections">
            <div className="container text-center">
                <h2 className="text-center title-sections">Blog</h2>
                <BlogHome></BlogHome>
                <a href="/blog" className="button">Ver Todos</a>
            </div>
        </section>

        <section className="p-sections">
            <div className="calculo">
                <div className="container">
                    <div className="row d-flex-center">
                        <div className="col-md-5">
                            <h2 className="c-white">Faça agora nosso cálculo de redução de custo mensal</h2>
                        </div>
                        <div className="col-md-7">
                        <form onSubmit={this.handleSubmit}>
                            <div className="form-calculo">
                                <div className="row">
                                    <div className="col-md-6">
                                        <label>Quantidade <br /> de Veículos</label>
                                        <input type="text" value={this.state.field1} onChange={this.handleChange} />
                                    </div>
                                    <div className="col-md-6">
                                        <label>Custo médio de frete p/veículo/mês</label>
                                        <input type="text" value={this.state.field2} onChange={this.handleChange} />
                                    </div>
                                    <div className="col-md-6">
                                        <label>Custo médio folha de pagamento mês</label>
                                        <input />
                                    </div>
                                    <div className="col-md-6">
                                        <label>Custo médio abastecimento p/veículo/mensal</label>
                                        <input />
                                    </div>
                                    <div className="col-md-6">
                                        <label>Custo médio manutenção p/veículo/mensal</label>
                                        <input />
                                    </div>
                                    <div className="col-md-6">
                                        <input type="submit" value="Calcular" className="btn-calcular"/>
                                    </div>
                                    <div className="col-md-12 text-center">
                                        <label>Redução de custo mensal</label>
                                        <h3 className="text-center"> { resultado }34 </h3>
                                    </div>
                                </div>
                            </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </section>

        <Footer></Footer>


    </div>);
    }
}

export default Home;
