import React from 'react';
import ReactSwipe from 'react-swipe';
import './Testimonials.css';
import axios from 'axios';

class Testimonials extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      testimonys: []
    }
  }

  componentDidMount() {
    axios.get(`https://api.elithium.tk/api/testimony`)
      .then(res => {
        const testimonys = res.data;
        this.setState({ testimonys });
        console.log(testimonys);
      })
  }

  render() {
    let reactSwipeEl;
    return (

      <div className="testimonials">
        <h1> Depoimentos </h1>
        <ReactSwipe
          className="carousel"
          swipeOptions={{ continuous: true, auto: 5000, }}
          ref={el => (reactSwipeEl = el)}
        >
          {this.state.testimonys.map(testimony =>
            <div key={testimony._id}>
              <h2>{testimony.title}</h2>
              <span className="manager">{testimony.author}</span>
              <p>{testimony.content}</p>
            </div>

          )}
        </ReactSwipe>
        {/* <button onClick={() => reactSwipeEl.next()}>Next</button>
          <button onClick={() => reactSwipeEl.prev()}>Previous</button> */}
      </div>
    );
  }
}

export default Testimonials;