import React from 'react';
import Footer from './components/Footer';
import Header from './components/Header';
import SliderSensorFadiga from './components/sliders/SliderSensorFadiga';

const SensorFadiga = () =>
  <div className="SensorFadiga">
    <Header title='Sensor de Fadiga e Distracão'></Header>

    <section className="p-sections">
      <div className="container">
        <div className="row">
          <div className="col-md-5">
            <img src="/sensor-fadiga.png" alt="sensor-fadiga" className="img-responsive" />
          </div>
          <div className="col-md-7">
            <h2 className="title-sections">Monitoramento e reconhecimento inteligente</h2>
            <p>O sensor de fadiga é um sistema que identifica mudanças no padrão de direção do motorista.
              Quando elas acontecem, ele deduz que chegou a hora de fazer uma parada, esticar as pernas e
               tomar um café, ou seja, descansar um pouco antes de continuar a viagem.</p>
            <p>A função de detecção de fadiga alerta o condutor para uma perda de concentração a
               partir dos 65 km/h. Ele analisa parâmetros como o comportamento de direção,
               uso do pedal e aceleração lateral. Se o sistema interpretar que a concentração
               do motorista está diminuindo, ele manda um aviso visual no painel e um aviso sonoro.
              </p>
          </div>
        </div>
      </div>
    </section>

    <section className="bg-sections-gradient">
      <div className="container">
        <div className="row">
          <div className="col-md-offset-1 col-md-4">
            <h2 className="title-sections c-white">Reconhecimento Facial e Visual</h2>
            <p className="c-white"> Identificação das pupilas e monitoramento comportamental do condutor/piloto.
            </p>
            <p className="c-white"> Identificação de sinais de sonolência
            e/ou distrações (como utilização de celular ou desvio do olhar da direção). </p>
          </div>
          <div className="col-md-offset-2 col-md-4">
            <h2 className="title-sections c-white"> Reconhecimento de Obstruções e Erros</h2>
            <p className="c-white">
              A não identificação da pupila do condutor/piloto são
              entendidas pelo sensor. Como obstruções na frente
              da câmera, deslocamento do sensor ou uso de
              acessórios inapropriados à condução. </p>
          </div>
        </div>
      </div>
    </section>

    <section>
      <div className="container">
        <div className="row">
          <div className="col-md-offset-2 col-md-8">
            <SliderSensorFadiga></SliderSensorFadiga>
          </div>
        </div>
      </div>
    </section>

    <section className="p-sections">
      <div className="bg-call-to-action-section bg-image-full">
        <div className="container">
          <div className="row">
            <div className="col-md-offset-7 col-md-4">
              <h2 className="title-sections">Baixe o guia completo do Sensor de Fadiga</h2>
              <a href="/docs/sensor-de-fadiga.pdf" target="_blank" className="rounded-button">Baixar Agora</a>
            </div>
          </div>
        </div>
      </div>
    </section>

    <Footer></Footer>

  </div>;


export default SensorFadiga;
