import React from 'react';
import axios from 'axios';
import Header from './components/Header';
import Footer from './components/Footer';
import { Twitter, Facebook, Linkedin, Google } from 'react-social-sharing'

export class Blog extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      article: null,
    }
  }

  componentDidMount() {
    const postId = this.props.location.state.blogId;
    axios.get(`https://api.elithium.tk/api/blog/${postId}`)
      .then(res => {
        this.setState({
          article: res.data
        })
      })
  }

  render() {
    const { article } = this.state;

    return <div>

      <div className="m-b-70">
        <Header title='Blog'></Header>
      </div>

      <div className="container m-b-70">
        {!!article && <div className="row">
          <div className="col-md-8">
            <h2 className="title-sections">{article.title}</h2>
            <p dangerouslySetInnerHTML={{ __html: article.content }} ></p>
            <div className="share-social-media">
              <h4>Compartilhe nas redes sociais</h4>
              <Twitter link="" />
              <Facebook link="" />
              <Linkedin link="" />
              <Google link="" />
            </div>

          </div>
          <div className="col-md-offset-1 col-md-3">
            <div className="blog-category-title">
              <h3>Categorias</h3>
            </div>
            {article.categories.map(c =>
              <p className="blog-category">{c.title}</p>
            )}
          </div>
        </div>}

      </div>
      <Footer></Footer>
    </div>
  }
}