import React from 'react';
import './HeaderHome.css';

class HeaderHome extends React.Component {

  constructor() {
    super();

    this.state = {
      showMenu: false,
      showMenuMobile: false
    }

    this.showMenu = this.showMenu.bind(this);
    this.showMenuMobile = this.showMenuMobile.bind(this);
  }

  showMenu(event) {
    event.preventDefault();

    this.setState({
      showMenu: !this.state.showMenu,
    });
  }

  showMenuMobile(event) {
    event.preventDefault();

    this.setState({
      showMenuMobile: !this.state.showMenuMobile,
    });
  }

  render() {

    return (

  <div className="main-header">
    <header>
      <div className="container">
        <div className="row">
          <div className="col-md-3">
            <a href="/" className="show">
              <img src="/logo-white.png" alt="logo" className="img-responsive m-0-auto" />
            </a>
          </div>
          <div className="col-md-7 col-md-offset-2 none-mobile">
            <div className="login-container"><a href="#" className="login-home">login</a></div>
            <nav className="menu-home">
              <ul>
                <li><a href="/">Home</a></li>
                <li><a href="/sobre">A Empresa</a></li>
                <li className="sub-menu"><a href="/" onClick={this.showMenu}>Soluções</a>
                      {
                        this.state.showMenu
                          ? (
                            <ul>
                              <li> <a href="/solucoes/sistema-ssx">Sistema SSX </a> </li>
                              <li> <a href="/solucoes/apps-onboarding-tracking">Apps Elithium </a> </li>
                              <li> <a href="/solucoes/sensor-fadiga">Sensor de Fadiga </a> </li>
                            </ul>
                          )
                          : (
                            null
                          )
                      }
                    </li>
                <li><a href="/cases">Cases</a></li>
                <li><a href="/#contact">Contato</a></li>
              </ul>
            </nav>
          </div>
          <div className="col-md-12 show-mobile">
            <button className="button-mobile-home"  onClick={this.showMenuMobile}><i className="fa fa-bars" aria-hidden="true"></i></button> 
            {
              this.state.showMenuMobile ? (
            <ul className="menu-mobile-home">
              <li><a href="/">Home</a></li>
              <li><a href="/sobre">A Empresa</a></li>
              <li> <a href="/solucoes/sistema-ssx">Sistema SSX </a> </li>
              <li> <a href="/solucoes/apps-onboarding-tracking">Apps Elithium </a> </li>
              <li> <a href="/solucoes/sensor-fadiga">Sensor de Fadiga </a> </li>
              <li><a href="/cases">Cases</a></li>
              <li><a href="/#contact">Contato</a></li>
              <a href="#" className="login-home">login</a>
              </ul>
              ) : ( null ) } 
          </div>
        </div>
      </div>
    </header>
    <div className="container text-chamada-home">
      <div className="row">
        <div className="col-md-offset-3 col-md-6">
          <h3 className="title-sections text-center">Tecnologia é nossa especialidade</h3>
          <p>Nós vamos ajudar a sua empresa crescer</p>
          <p>Otimize e reduza os custos da sua frota</p>
          <a href="">Conheça nossas Soluções</a>
        </div>
      </div>
    </div>
  </div>);
  }
}

export default HeaderHome;
