import React from 'react';
import ReactDOM from "react-dom";
// import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Home from './Home';
import Sobre from './Sobre';
import Cases from './Cases';
import Ssx from './Ssx';
import Onboarding from './Onboarding';
import SensorFadiga from './SensorFadiga';
import { Blog } from './Blog.jsx';
import PageBlog from './PageBlog';
import './App.css';
import './assets/css/bootstrap/css/bootstrap.css';

import { BrowserRouter as Router, Route, Link, Switch } from "react-router-dom";

const App = () => (
  <Router>
    <Switch>
    <Route exact path="/" component={Home} />
    <Route path="sobre" component={Sobre} />
    <Route path="/:cases" component={Cases} />
    <Route path="/blog/:title" component={Blog} />
    <Route path="/:solucoes/:sistema-ssx" component={Ssx} />
    <Route path="/:solucoes/:apps-onboarding-tracking" component={Onboarding} />
    <Route path="/:solucoes/:sensor-fadiga" component={SensorFadiga} />
    <Route path="/blog" component={PageBlog} />
    </Switch>
  </Router>
)

export default App;

