import React from 'react';
import ReactSwipe from 'react-swipe';
import './Sliders.css';

class SliderSsx extends React.Component {

  render() {
    let reactSwipeEl;
    return (

      <div className="sliders text-center">
        <ReactSwipe
          className="carousel"
          swipeOptions={{ continuous: true, auto: 5000, }}
          ref={el => (reactSwipeEl = el)}
        >
          <div>
            <h2>Rastreamento e Monitoramento</h2>
            <img src="/ssx/ssx1.jpg" alt="ssx1" className="img-responsive" />
            <p><strong>Tela Operacional Dinâmica</strong></p>
            <p>Permite que o usuário mude o layout
            conforme a sua necessidade. O
            sistema também permite verificar o
                histórico de até 3 veículos de uma vez.</p>
          </div>
          <div>
            <h2>Rastreamento e Monitoramento</h2>
            <img src="/ssx/ssx2.jpg" alt="ssx2" className="img-responsive" />
            <p><strong>Tela Operacional Dinâmica</strong></p>
            <p>Permite que o usuário mude o layout
            conforme a sua necessidade. O
            sistema também permite verificar o
                histórico de até 3 veículos de uma vez.</p>
          </div>
          <div>
            <h2>Ranking de motoristas</h2>
            <img src="/ssx/ssx3.jpg" alt="ssx3" className="img-responsive" />
            <p>O Ranking de Motoristas apresenta
              dados e gráficos comparativos para
              cada motorista cadastrado.
              Nesta área da plataforma é possível
              comparar o rendimento entre
              motoristas, bem como: velocidade
              média de condução, infrações, tempo
              de motor ocioso e consumo por
                  quilometragem.</p>
          </div>
          <div>
            <h2>Ranking de motoristas</h2>
            <img src="/ssx/ssx4.jpg" alt="ssx3" className="img-responsive" />
            <p>O Ranking de Motoristas apresenta
              dados e gráficos comparativos para
              cada motorista cadastrado.
              Nesta área da plataforma é possível
              comparar o rendimento entre
              motoristas, bem como: velocidade
              média de condução, infrações, tempo
              de motor ocioso e consumo por
                  quilometragem.</p>
          </div>
          <div>
            <h2>Planejamento de Rotas</h2>
            <img src="/ssx/ssx5.jpg" alt="ssx5" className="img-responsive" />
            <p>O Planejamento de Rotas permite que o usuário acesse os dados de cada motorista, como: cadastro do
                motorista e veículo, tempo de percurso, previsão de chegada e retorno, itinerário, valores das cargas etc.</p>
          </div>
          <div>
            <h2>Planejamento de Rotas</h2>
            <img src="/ssx/ssx6.jpg" alt="ssx5" className="img-responsive" />
            <p>O usuário pode importar viagens via arquivo CSV ou por meio de integração Web Service,
              além da opção de configurar tudo manualmente</p>
          </div>
          <div>
            <h2>Planejamento de Rotas</h2>
            <img src="/ssx/ssx7.jpg" alt="ssx5" className="img-responsive" />
            <p>O sistema também possui a função de <strong>Viagem Expressa,</strong> que consiste em atender viagens rápidas,
              sendo o ponto A a localização do veículo e o ponto B o cliente a ser atendido.</p>
          </div>
          <div>
            <h2>Planejamento de Rotas</h2>
            <img src="/ssx/ssx8.jpg" alt="ssx5" className="img-responsive" />
            <p>O sistema compara todo o planejamento da viagem com o que realmente foi realizado,
destacando desvios de rota, serviços atrasados ou clientes não atendidos.</p>
          </div>
          <div>
            <h2>Jornada de Trabalho</h2>
            <img src="/ssx/ssx9.jpg" alt="ssx5" className="img-responsive" />
            <p>O <strong>Controle de Jornada de Trabalho </strong> é registrado no App Onboard, tanto para o motorista
como para o ajudante, gerando todo o relatório de ponto dos colaboradores.</p>
          </div>
        </ReactSwipe>
          <button onClick={() => reactSwipeEl.prev()} className="button-slider">
            <i className="fa fa-chevron-left"></i>
          </button>
          <button onClick={() => reactSwipeEl.next()} className="button-slider">
            <i className="fa fa-chevron-right"></i>
          </button> 
      </div>
    );
  }
}

export default SliderSsx;